<template>
	<div>
		<div class="register-title">
				<h3>账号注册</h3>
				<p>已有账号？
					<router-link class="rt-login" tag="span" to="login">立即登录</router-link>
				</p>
		</div>
		<div class="lr-content">
			<el-form :model='user' :rules='rules' @keyup.enter.native="register">
				<el-form-item prop="userName">
					<el-input v-model="user.userName" placeholder="账号"/>
				</el-form-item>
				<el-form-item prop="userPassword">
					<el-input type="password" v-model="user.userPassword" placeholder="密码" />
				</el-form-item>
				<el-form-item prop="userEmail">
					<el-input v-model="user.userEmail" placeholder="邮箱" />
				</el-form-item>
				<el-form-item prop="userTelephone">
					<el-input v-model="user.userTelephone" placeholder="手机号" />
				</el-form-item>
				<el-form-item>
					<el-button class="lr_btn" @click.native='register'>立即注册</el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>
	import { userRegister} from "@/network/user";
	export default {
		name: 'register',
		data() {
			return {
				user: {
					userName: '',
					userPassword: '',
					userEmail: '',
					userTelephone: ''
				},
				rules: {
					userName: [
						{ required: true, message: '账号不能为空', trigger: 'blur'},
						{ min: 3, max: 10, message: '账号长度在 3 到 10 个字符之间', trigger: 'blur'}
					],
					userPassword: [
						{ required: true, message: '密码不能为空', trigger: 'blur'},
						{ min: 3, max: 10, message: '密码长度在 3 到 10 个字符之间', trigger: 'blur'}
					],
					userEmail: [
						{ required: true, message: '邮箱地址不能为空', trigger: 'blur' },
						{ type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change']}
					],
					userTelephone: [
						{ required: true, message: '手机号码不能为空', trigger: 'blur'},
						{ pattern:/^1[3456789]\d{9}$/, message: '请输入正确的手机号码', trigger: 'blur'}
					],
				}
			}
		},
		methods: {
			register() {
				userRegister(this.user).then((res) => {
					this.$message({
						showClose: true,
						message: res.msg,
						duration: 1000
					});
					if (res.code === 1) {
						this.$router.push("/user/login");
					}
				});
			}
		}
	}
</script>

<style>
	.rt-login {
		color: #008489 !important;
		cursor: pointer;
	}
	.rt-login:hover {
		padding-bottom: 1px;
		border-bottom: 1px solid #008489;
	}
</style>
